( function( $ ) {
	function isInCustomizer() {
		return !! wp.customize;
	}

	$( document ).ready( function() {
		// Is home page and not displaying O2 filtered content
		if (
			isInCustomizer() ||
			( window.location.pathname === '/' &&
				window.location.search === '' )
		) {
			$( '.o2-app-page-title' ).addClass( 'is-unfiltered-home' );
		}
	} );
} )( jQuery );
