/**
 * Only show the editor footer when the editor is active or has content.
 */
( function( $ ) {
	$( document ).ready( () => {
		/**
		 * Selectors
		 */
		const editor = document.querySelector( '.o2-app-new-post' );
		if ( editor === null ) {
			return;
		}

		const $editorFooter = $( '.o2-app-new-post .o2-editor-footer' );
		const blockListLayoutSelector = '.block-editor-block-list__layout';
		const editorExpandedModifier = 'is-expanded';

		/**
		 * Local Storage Keys
		 */
		const newPostContentKey = `${ window._currentSiteId }-new`;

		/**
		 * Functions
		 */
		const isEditorEmpty = () => {
			const editorContent = window.localStorage.getItem(
				newPostContentKey
			);
			return editorContent === '';
		};

		const shouldExpandEditor = () =>
			editor.contains( document.activeElement ) || ! isEditorEmpty();

		const handleFocusChange = () => {
			if ( shouldExpandEditor() ) {
				$editorFooter.show();
				$( blockListLayoutSelector ).addClass( editorExpandedModifier );
			} else {
				$editorFooter.hide();
				$( blockListLayoutSelector ).removeClass(
					editorExpandedModifier
				);
			}
		};

		/**
		 * Main
		 */
		if ( isEditorEmpty() ) {
			$editorFooter.hide();
		}

		if ( editor ) {
			editor.addEventListener( 'focus', handleFocusChange, true );
			editor.addEventListener( 'blur', handleFocusChange, true );
		}
	} );
} )( jQuery );
