( function( $ ) {
	$( document ).ready( () => {
		const $editor = $( '.o2-app-new-post' );
		const $controls = $( '[data-p2020-mobile-new-post-controls]' );
		const btnNew = $controls.find( 'button' )[ 0 ];
		const btnCancel = $controls.find( 'button' )[ 1 ];

		// When user is not logged in
		if ( $editor.length === 0 ) {
			$controls.hide();
			return;
		}

		$editor.before( $controls );
		$controls.css( 'visibility', 'visible' ); // prevents FOUC before the DOM manipulation

		btnNew.addEventListener( 'click', () => {
			$editor.slideDown( 'fast' );
			$( btnNew ).hide();
			$( btnCancel ).show();
		} );

		btnCancel.addEventListener( 'click', () => {
			$editor.slideUp( 'fast' );
			$( btnCancel ).hide();
			$( btnNew ).show();
		} );
	} );
} )( jQuery );
